const palette = {
  primary: {
    fg: 'white',
    bg: 'linear-gradient(to right, #4568dc, #b06ab3)',
  },
  secondary: {
    fg: 'grey',
    bg: '#242424',
    bgDarker: '#181818',
  },
};
const baseSize = 18;

const typography = {
  baseFontSize: `${baseSize}px`,
};

export default {
  palette,
  typography,
  size: size => `${size * baseSize}px`,
};



